.header {
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  padding: 100px;
  height: 100vh;

  &__title {
    margin: 0;
    border-bottom: 1px solid var(--accent-color);
    font-family: var(--second-family);
    font-weight: 900;
    font-size: 9vw;
    text-transform: uppercase;
    color: var(--text-color);
  }

  &__subtitle {
    margin: 0;
    font-weight: 400;
    font-size: 1.3vw;
    text-transform: uppercase;
    color: var(--subtext-color);
  }

  &__button-wrapper {
    justify-content: space-between;
    width: 100%;
    max-width: 360px;
  }

  &__btn {
    border-radius: 8px;
    padding: 15px 20px;
    font-weight: 600;
    font-size: 12px;
    text-transform: uppercase;
    background-color: var(--btn-bg-color);
  }
}
